@keyframes arrow {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch_minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: auto;
  animation: 3600s steps(60, end) infinite arrow;
  position: absolute;
  inset: 0 0 20vmin;
}

.stopwatch_seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 40vmin;
  margin: auto;
  animation: 60s linear infinite arrow;
  position: absolute;
  inset: 0 0 40vmin;
}

.stopwatch_center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

.stopwatch--speed-up .stopwatch_minute {
  animation: 600s steps(60, end) infinite arrow;
}

.stopwatch--speed-up .stopwatch_seconds {
  animation: 10s linear infinite arrow;
}

